<template>
	<div id="estore-diy">
		<public-header :title="title"></public-header>
		<div class="content font-14">
			<template v-for="(diyitem, diyitemIndex) in diy.data.items">
				<template v-if="diyitem.id == 'basic'">
					<div class="address">
						<template v-if="mode == 'send'">
							<!--帮我送-->
							<template v-if="storeNum > 1">
								<div class="address-item van-hairline--bottom" @click="util.jsUrl('/plugin/pages/paotui/store/shop/select', query)">
									<div class="left">
										<div class="mark" :style="{color: diyitem.style.from.color, background: diyitem.style.from.background}">发</div>
									</div>
									<i class="icon icon-right"></i>
									<div class="right">
										<div class="title" :style="{color: diyitem.style.address.titlecolor}">{{buyaddress.title}}</div>
										<div class="desc margin-10-t" :style="{color: diyitem.style.address.desccolor}">{{buyaddress.address}}</div>
										<div class="relation margin-10-t" :style="{color: diyitem.style.address.titlecolor}">{{buyaddress.telephone}}</div>
									</div>
								</div>
							</template>
							<template v-else>
								<div class="address-item van-hairline--bottom">
									<div class="left">
										<div class="mark" :style="{color: diyitem.style.from.color, background: diyitem.style.from.background}">发</div>
									</div>
									<div class="right">
										<div class="title" :style="{color: diyitem.style.address.titlecolor}">{{buyaddress.title}}</div>
										<div class="desc margin-10-t" :style="{color: diyitem.style.address.desccolor}">{{buyaddress.address}}</div>
										<div class="relation margin-10-t" :style="{color: diyitem.style.address.titlecolor}">{{buyaddress.telephone}}</div>
									</div>
								</div>
							</template>
							<div class="address-item">
								<div class="left">
									<div class="mark" :style="{color: diyitem.style.to.color, background: diyitem.style.to.background}">收</div>
								</div>
								<div class="right">
									<div class="address-type margin-15-b" v-if="diyitem.params.address.type == 'both'">
										<div :class="{'type-item': 1, active: erranderStoreExtra.address_type == 'select'}" @click="onToggleAddressType('select')">从已有地址选择</div>
										<div :class="{'type-item': 1, active: erranderStoreExtra.address_type == 'input'}" @click="onToggleAddressType('input')">手动填写</div>
									</div>
									<template v-if="erranderStoreExtra.address_type == 'select'">
										<div class="flex-lr" @click="util.jsUrl( '/pages/member/address', {channel: 'errander', input: 'accept', erranderId: id, mode: mode}, 'replace')" v-if="acceptaddress && acceptaddress.address">
											<div>
												<div class="title" :style="{color: diyitem.style.address.titlecolor}">
													{{acceptaddress.address}}-{{acceptaddress.number}}
												</div>
												<div class="desc margin-10-t" :style="{color: diyitem.style.address.desccolor}">
													<span>{{acceptaddress.realname}}</span>
													<span>{{acceptaddress.sex}}</span>
													<span>{{acceptaddress.mobile}}</span>
												</div>
											</div>
											<div class="icon icon-arrow-right c-gray font-12"></div>
										</div>
										<template v-else>
											<div class="flex-lr" @click="util.jsUrl( '/pages/member/address', {channel: 'errander', input: 'accept', erranderId: id, mode: mode}, 'replace')">
												<div class="desc c-gray">选择收件地址</div>
												<div class="icon icon-arrow-right c-gray font-12"></div>
											</div>
										</template>
									</template>
									<template v-else>
										<div class="inputs">
											<div class="flex-lr padding-15-b van-hairline--bottom">
												<div class="realname">
													<input v-model="erranderStoreExtra.address_input.realname" type="text" placeholder="姓名">
												</div>
												<div class="mobile van-hairline--left">
													<input v-model="erranderStoreExtra.address_input.mobile" type="text" placeholder="手机号">
												</div>
											</div>
											<div class="flex-lr padding-15-tb van-hairline--bottom" @click="util.jsUrl('/pages/member/addressLocation', {channel: 'erranderStoreInput', mode: mode})">
												<div v-if="erranderStoreExtra.address_input.address">{{erranderStoreExtra.address_input.address}}</div>
												<div class="c-gray" v-else>选择小区或办公楼名称</div>
												<div class="icon icon-arrow-right c-gray font-12"></div>
											</div>
											<div class="flex-lr padding-15-t">
												<input v-model="erranderStoreExtra.address_input.number" type="text" placeholder="输入楼层或门牌号">
											</div>
										</div>
									</template>
								</div>
							</div>
						</template>
						<template v-else-if="mode == 'take'">
							<!--帮我取-->
							<div class="address-item van-hairline--bottom" >
								<div class="left">
									<div class="mark" :style="{color: diyitem.style.from.color, background: diyitem.style.from.background}">发</div>
								</div>
								<div class="right">
									<div class="address-type margin-15-b" v-if="diyitem.params.address.type == 'both'">
										<div :class="{'type-item': 1, active: erranderStoreExtra.address_type == 'select'}" @click="onToggleAddressType('select')">从已有地址选择</div>
										<div :class="{'type-item': 1, active: erranderStoreExtra.address_type == 'input'}" @click="onToggleAddressType('input')">手动填写</div>
									</div>
									<template v-if="erranderStoreExtra.address_type == 'select'">
										<template v-if="buyaddress && buyaddress.address">
											<div class="flex-lr" @click="util.jsUrl( '/pages/member/address', {channel: 'errander', input: 'buy', erranderId: id, mode: mode}, 'replace')">
												<div>
													<div class="title" :style="{color: diyitem.style.address.titlecolor}">{{buyaddress.address}}-{{buyaddress.number}}</div>
													<div class="desc margin-10-t" :style="{color: diyitem.style.address.desccolor}">
														<span>{{buyaddress.realname}}</span>
														<span>{{buyaddress.sex}}</span>
														<span>{{buyaddress.mobile}}</span>
													</div>
												</div>
												<div class="icon icon-arrow-right c-gray font-12"></div>
											</div>
										</template>
										<template v-else>
											<div class="flex-lr" @click="util.jsUrl( '/pages/member/address', {channel: 'errander', input: 'buy', erranderId: id, mode: mode}, 'replace')">
												<div class="desc c-gray">选择取件地址</div>
												<div class="icon icon-arrow-right c-gray font-12"></div>
											</div>
										</template>
									</template>
									<template v-else>
										<div class="inputs">
											<div class="flex-lr padding-15-b van-hairline--bottom">
												<div class="realname">
													<input v-model="erranderStoreExtra.address_input.realname" type="text" placeholder="姓名">
												</div>
												<div class="mobile van-hairline--left">
													<input v-model="erranderStoreExtra.address_input.mobile" type="text" placeholder="手机号">
												</div>
											</div>
											<div class="flex-lr padding-15-tb van-hairline--bottom" @click="util.jsUrl('/pages/member/addressLocation', {channel: 'erranderStoreInput', mode: mode})">
												<div v-if="erranderStoreExtra.address_input.address">{{erranderStoreExtra.address_input.address}}</div>
												<div class="c-gray" v-else>选择小区或办公楼名称</div>
												<div class="icon icon-arrow-right c-gray font-12"></div>
											</div>
											<div class="flex-lr padding-15-t">
												<input v-model="erranderStoreExtra.address_input.number" type="text" placeholder="输入楼层或门牌号">
											</div>
										</div>
									</template>
								</div>
							</div>
							<template v-if="storeNum > 1">
								<div class="address-item" @click="util.jsUrl('/plugin/pages/paotui/store/shop/select', query)">
									<div class="left">
										<div class="mark" :style="{color: diyitem.style.to.color, background: diyitem.style.to.background}">收</div>
									</div>
									<i class="icon icon-right"></i>
									<div class="right">
										<div class="title" :style="{color: diyitem.style.address.titlecolor}">{{acceptaddress.title}}</div>
										<div class="desc margin-10-t" :style="{color: diyitem.style.address.desccolor}">{{acceptaddress.address}}</div>
										<div class="relation margin-10-t" :style="{color: diyitem.style.address.titlecolor}">{{acceptaddress.telephone}}</div>
									</div>
								</div>
							</template>
							<template v-else>
								<div class="address-item">
									<div class="left">
										<div class="mark" :style="{color: diyitem.style.to.color, background: diyitem.style.to.background}">收</div>
									</div>
									<div class="right">
										<div class="title" :style="{color: diyitem.style.address.titlecolor}">{{acceptaddress.title}}</div>
										<div class="desc margin-10-t" :style="{color: diyitem.style.address.desccolor}">{{acceptaddress.address}}</div>
										<div class="relation margin-10-t" :style="{color: diyitem.style.address.titlecolor}">{{acceptaddress.telephone}}</div>
									</div>
								</div>
							</template>
						</template>
					</div>
					<van-cell-group class="margin-10-t">
						<van-cell is-link clickable @click="onTogglePopup('type')">
							<div slot="title" :style="{color: diyitem.style.type.titlecolor}">{{diyitem.params.type.title}}</div>
							<template v-if="erranderStoreExtra.goods_type && erranderStoreExtra.goods_weight > 0">
								<div slog="-">{{erranderStoreExtra.goods_type}}/{{erranderStoreExtra.goods_weight}}公斤</div>
							</template>
							<template v-else>
								<div slog="-" :style="{color: diyitem.style.type.desccolor}">{{diyitem.params.type.desc}}</div>
							</template>
							<i class="icon margin-10-r font-18" :class="diyitem.params.type.icon" slot="icon" :style="{color: diyitem.style.type.titlecolor}"></i>
						</van-cell>
					</van-cell-group>
					<van-cell-group class="margin-10-t">
						<van-cell is-link clickable @click="onTogglePopup('source')">
							<div slot="title" :style="{color: diyitem.style.source.titlecolor}">{{diyitem.params.source.title}}</div>
							<template v-if="erranderStoreExtra.goods_source">
								<div slog="-">
									{{erranderStoreExtra.goods_source}}
									<template v-if="erranderStoreExtra.serial_sn">#{{erranderStoreExtra.serial_sn}}</template>
								</div>
							</template>
							<template v-else>
								<div slog="-" :style="{color: diyitem.style.source.desccolor}">{{diyitem.params.source.desc}}</div>
							</template>
							<i class="icon margin-10-r font-18" :class="diyitem.params.source.icon" slot="icon" :style="{color: diyitem.style.source.titlecolor}"></i>
						</van-cell>
						<van-cell class="delivery-time-cell" is-link @click="onTogglePopup('deliveryTime')">
							<div slot="title" :style="{color: diyitem.style.taketime.titlecolor}">{{diyitem.params.taketime.title}}</div>
							<template v-if="erranderStoreExtra.delivery_day && erranderStoreExtra.delivery_time">
								<div slog="-">{{erranderStoreExtra.delivery_day}} {{erranderStoreExtra.delivery_time == order.delivery_nowtime ? '立即送出(约'+order.delivery_nowtime+')' : erranderStoreExtra.delivery_time}}</div>
							</template>
							<template v-else>
								<div slog="-" :style="{color: diyitem.style.taketime.desccolor}">{{diyitem.params.taketime.desc}}</div>
							</template>
							<i class="icon margin-10-r font-18" :class="diyitem.params.taketime.icon" slot="icon" :style="{color: diyitem.style.taketime.titlecolor}"></i>
						</van-cell>
						<van-cell is-link>
							<div slot="title" :style="{color: diyitem.style.redpacket.titlecolor}">{{diyitem.params.redpacket.title}}</div>
							<div slog="-" :style="{color: diyitem.style.redpacket.desccolor}">{{diyitem.params.redpacket.desc}}</div>
							<i class="icon margin-10-r font-18" :class="diyitem.params.redpacket.icon" slot="icon" :style="{color: diyitem.style.redpacket.titlecolor}"></i>
						</van-cell>
						<van-cell is-link @click="onTogglePopup('tip')" v-if="diyitem.params.tip.status == 1">
							<div slot="title" :style="{color: diyitem.style.tip.titlecolor}">{{diyitem.params.tip.title}}</div>
							<div slog="-" :style="{color: erranderStoreExtra.delivery_tips > 0 ? diy.data.page.activebackground : diyitem.style.tip.desccolor}">
								{{erranderStoreExtra.delivery_tips > 0 ? Lang.dollarSign + erranderStoreExtra.delivery_tips : diyitem.params.tip.desc}}
							</div>
							<i class="icon margin-10-r font-18" :class="diyitem.params.redpacket.icon" slot="icon" :style="{color: diyitem.style.redpacket.titlecolor}"></i>
						</van-cell>
					</van-cell-group>
					<van-cell-group class="margin-10-t">
						<van-cell is-link @click="util.jsUrl('/plugin/pages/paotui/store/order/note', query, 'replace')">
							<div slot="title" :style="{color: diyitem.style.note.titlecolor}">{{diyitem.params.note.title}}</div>
							<template v-if="erranderStoreExtra.note">
								<div slog="-">{{erranderStoreExtra.note}}</div>
							</template>
							<template v-else>
								<div slog="-" :style="{color: diyitem.style.note.desccolor}">{{diyitem.params.note.desc}}</div>
							</template>
							<i class="icon margin-10-r font-18" :class="diyitem.params.note.icon" slot="icon" :style="{color: diyitem.style.note.titlecolor}"></i>
						</van-cell>
					</van-cell-group>
				</template>
			</template>
		</div>
		<div class="submit-bar">
			<div class="left">
				<div>
					<div>
						<span class="font-12" :style="{color: basicPart.style.finalfee.namecolor}">{{basicPart.params.finalfeetext}}</span>
						<span class="font-12" :style="{color: basicPart.style.finalfee.pricecolor}">{{Lang.dollarSign}}</span><span class="final-fee">{{order.final_fee}}</span>
					</div>
					<div class="distance" :style="{color: basicPart.style.distancecolor}">{{order.distance > 0 ? order.distance + 'km' : '请完善地址信息'}}</div>
				</div>
				<div class="flex font-12" @click="onTogglePopup('fee')" :style="{color: basicPart.style.pricedetailcolor}">
					<span>价格明细</span>
					<i class="icon icon-right font-12"></i>
				</div>
			</div>
			<div class="right" @click="onSubmit" :class="{disabled: !islegal}" :style="{color: basicPart.style.submit.color, background: basicPart.style.submit.background}">{{basicPart.params.submittext}}</div>
		</div>

		<!--物品类型弹出层-->
		<van-popup v-model="popup.type" position="bottom" class="popup-type">
			<div class="popup-title border-1px-b">
				<span class="font-14 c-gray" @click="onTogglePopup('type')">取消</span>
				<span>{{basicPart.params.type.title}}</span>
				<span class="font-14" @click="onChangeTypeAndWeight">确定</span>
			</div>
			<div class="popup-content">
				<div class="types">
					<div :class="{'type-item': 1, active: goodsType == item.name}" v-for="(item, index) in basicPart.params.type.options" :key="index">
						<div class="item-inner" @click="onSelectOption('goodsType', item.name)">{{item.name}}</div>
					</div>
				</div>
				<div class="padding-15 flex-lr">
					<div>重量（公斤）</div>
					<van-stepper
						v-model="goodsWeight"
						integer
						:min="1"
						:max="20"
						:step="1"
						:disable-input="true"
					/>
				</div>
				<div class="padding-15 flex-lr">
					<div>是否设置为默认</div>
					<van-switch
						v-model="isdefault"
						size="24px"
						active-color="#ff2d4b"
						:active-value="1"
						:inactive-value="0"
					>
					</van-switch>
				</div>
			</div>
		</van-popup>

		<!--物品来源弹出层-->
		<van-popup v-model="popup.source" position="bottom" class="popup-type popup-source">
			<div class="popup-title border-1px-b">
				<span class="font-14 c-gray" @click="onTogglePopup('source')">取消</span>
				<span>{{basicPart.params.source.title}}</span>
				<span class="font-14" @click="onChangeSourceAndSerialsn">确定</span>
			</div>
			<div class="popup-content">
				<div class="types">
					<div :class="{'type-item': 1, active: item.title == goodsSource}" v-for="(item, index) in sources" :key="index">
						<div class="item-inner" @click="onSelectOption('goodsSource', item.title)">{{item.title}}</div>
					</div>
				</div>
				<div class="serial-sn" v-if="goodsSource == '本平台'">
					<span class="c-gray">系统将自动为您分配流水号</span>
				</div>
				<div class="serial-sn" v-else>
					<input v-model="serialSn" type="text" placeholder="请填写流水号（选填）">
				</div>
			</div>
		</van-popup>

		<!--取件时间弹出层-->
		<van-popup v-model="popup.deliveryTime" position="bottom" class="popup-delivery-time" v-if="order.delivery_info && erranderStoreExtra.delivery_day">
			<div class="popup-title flex-lr border-1px-b">
				<span class="font-14" @click="onTogglePopup('deliveryTime')">取消</span>
				<span>{{basicPart.params.taketime.title}}</span>
				<span class="opacity-1">确定</span>
			</div>
			<div class="popup-content flex-lr">
				<div class="date">
					<div class="date-item" :class="{active: day == erranderStoreExtra.delivery_day}" v-for="(item, day) in order.delivery_info" @click="onSelectDay(day)">{{day}}</div>
				</div>
				<div class="time" v-if="order.delivery_info[erranderStoreExtra.delivery_day] && order.delivery_info[erranderStoreExtra.delivery_day].times">
					<div
						class="time-item"
						:class="{active: item == erranderStoreExtra.delivery_time}"
						:style="{color: (item == erranderStoreExtra.delivery_time && diy.data.page && diy.data.page.activebackground) ? diy.data.page.activebackground : '#333'}"
						v-for="(item, index) in order.delivery_info[erranderStoreExtra.delivery_day].times"
						:key="index"
						@click="onSelectTime(item)"
					>
						{{item == order.delivery_nowtime ? '立即送出(大约' + item + ')' : item}}
						<span
							class="icon"
							:class="{'icon-check': item == erranderStoreExtra.delivery_time}"
							:style="{color: (item == erranderStoreExtra.delivery_time && diy.data.page && diy.data.page.activebackground) ? diy.data.page.activebackground : '#fff'}"
						></span>
					</div>
				</div>
			</div>
		</van-popup>

		<!--小费弹出层-->
		<van-popup v-model="popup.tip" position="bottom" class="popup-tip">
			<div class="popup-title flex-lr border-1px-b">
				<span class="font-14 c-gray" @click="onTogglePopup('tip')">取消</span>
				<span>{{basicPart.params.tip.title}}</span>
				<span class="font-14 c-danger" @click="onChangeTip">确定</span>

			</div>
			<div class="popup-content padding-10">
				<ul class="tip-group" v-if="basicPart.tipsGroup.length > 0">
					<li class="tip-item-wrap" v-for="(item, index) in basicPart.tipsGroup" :key="index" @click="onSelectTips(item, index)">
						<div :class="{'tip-item-inner': 1, active: (tipsInfo.selectIndex == index && tipsInfo.dataFrom == 'select')}">{{item > 0 ? Lang.dollarSign + item : '不加了'}}</div>
					</li>
				</ul>
				<div :class="{'tip-input': 1, active: tipsInfo.inputFocus}" @click="onTipsInputFocus">
					<span>其他金额</span>
					<template v-if="tipsInfo.inputFocus">
						<span class="margin-10-l">{{Lang.dollarSign}}</span>
						<input type="number" v-model="tipsInfo.input" :autofocus="tipsInfo.inputFocus" @blur="onBlur" @focus="onFocus">
					</template>
				</div>
				<div class="flex-center font-12 margin-10-t margin-5-b c-gray">
					<i class="icon icon-warn margin-5-r"></i>
					<span>
						最低{{basicPart.params.tip.min}}{{Lang.dollarSignCn}}，
						最高{{basicPart.params.tip.max}}{{Lang.dollarSignCn}}
					</span>
				</div>
			</div>
		</van-popup>

		<!--费用明细弹出层-->
		<van-popup v-model="popup.fee" position="bottom" class="popup-fee">
			<div class="popup-title flex-lr border-1px-b">
				<span class="font-12 flex opacity-1">
					价格规则
					<span class="icon icon-right font-12 "></span>
				</span>
				<span>费用明细</span>
				<span
					class="font-12 flex opacity-0"
					:style="{color: (diy.data.page && diy.data.page.activebackground) ? diy.data.page.activebackground : '#ff2d4b'}"
					@click="util.jsUrl('/plugin/pages/paotui/store/order/feeRule')"
				>
					价格规则
					<span class="icon icon-right font-12 "></span>
				</span>
			</div>
			<div class="popup-content">
				<van-cell-group class="border-0px">
					<template v-for="(item, index) in order.fees">
						<van-cell
							class="border-0px"
							v-if="item.fee != ''"
							:style="{color: (item.fee < 0 && diy.data.page && diy.data.page.activebackground) ? diy.data.page.activebackground : '#969799'}"
							:title="item.title"
							:value="item.fee_cn"
						/>
					</template>
				</van-cell-group>
			</div>
		</van-popup>

		<!-- 红包弹出层 -->
		<van-popup v-model="popup.redpacket" position="bottom" v-if="redPackets.length > 0">
			<div class="popup-redpacket">
				<div class="popup-title van-hairline--bottom text-center">可用红包</div>
				<div class="popup-container">
					<load type="loaded" :text="'可用红包(' + redPackets.length + '个)'" bgcolor="#f5f5f5"></load>
					<div class="redPacket-list content-padded" v-for="item in redPackets" :key="item.id">
						<div class="redPacket-list-item" @click="onSelectRedpacket(item.id)">
							<div class="redPacket-list-item-container">
								<div class="redPacket-info row">
									<div class="col-50">
										<span class="redPacket-title">{{item.title}}</span>
									</div>
									<div class="col-50 text-right">
										<div class="price">{{Lang.dollarSign}}<span class="price-num">{{item.discount}}</span></div>
									</div>
								</div>
								<div class="redPacket-use-limit row">
									<div class="col-60">{{item.day_cn}}</div>
									<div class="col-40 text-right">
										<p class="use-condition">满{{item.condition}}{{Lang.dollarSignCn}}可用</p>
									</div>
								</div>
							</div>
							<span class="circle circle-left"></span>
							<span class="circle circle-right"></span>
							<div class="selected-status" v-if="item.id == order.redpacket_id">
								<img src="static/img/success.png" alt="" class="img-100">
							</div>
						</div>
					</div>
				</div>
				<div class="popup-cancle van-hairline--top" @click="onSelectRedpacket(0)">不使用红包</div>
			</div>
		</van-popup>
		<failed-tips :failedTips="failedTips" v-if="!getLocationStatus"></failed-tips>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from "@/components/header"
	import failedTips from '@/components/failedTips'
	export default {
		components: {
			PublicHeader, failedTips
		},
		data() {
			return {
				id: 0,
				order_id: 0,
				mode: 'take',
				order: {
					delivery_info: {}
				},
				diy: {
					data: {},
				},
				basicPart: {
					params: {
						type: {
							title: '物品类型',
							desc: '请选择物品类型',
							icon: 'icon-present',
							is_required: 1,
							options: {
								C0123456789101: {
									name: '蔬菜水果'
								},
								C0123456789102: {
									name: '快餐食物'
								},
								C0123456789103: {
									name: '快递包裹'
								}
							}
						},
						source: {
							title: '物品来源',
							desc: '请选择物品来源',
							icon: 'icon-newshot'
						},
						taketime: {
							title: '取件时间',
							desc: '立即取件',
							icon: 'icon-time'
						},
						redpacket: {
							title: '红包',
							desc: '暂无可用红包',
							icon: 'icon-redpacket'
						},
						tip: {
							title: '小费',
							desc: '加小费有助于更快接单',
							icon: 'icon-moneybag',
							min: 0,
							max: 20,
							status: 1,
						},
						note: {
							title: '备注',
							desc: '给配送员留言',
							icon: 'icon-form'
						},
						agreement: '',
						submittext: '提交订单',
						finalfeetext: '跑腿费',
						pricedetailtext: '价格明细'
					},
					style: {
						submit: {
							color: '#ffffff',
							background: '#ff2d4b',
						},
						finalfee: {
							namecolor: '#333333',
							pricecolor: '#ff2d4b'
						},
						distancecolor: '#333333',
						pricedetailcolor: '#999999',
					},
					tipsGroup: []
				},
				tipsInfo: {
					selectIndex: 0,
					value: '',
					input: '',
					inputFocus: false,
					dataFrom: 'select'
				},
				redPackets:[],
				goodsWeight: 0,
				goodsType: '',
				goodsSource: '',
				serialSn: '',
				title: '商户发单',
				popup: {
					type: false,
					source: false,
					deliveryTime: false,
					tip: false,
					fee: false,
					redpacket: false
				},
				sources: {
					we7_wmall: {
						key: 'we7_wmall',
						title: '本平台',
					},
					meituan: {
						key: 'meituan',
						title: '美团',
					},
					eleme: {
						key: 'eleme',
						title: '饿了么',
					},

				},
				isdefault: false,
				storeNum: 1,
				query: {},
				islegal: false,
				showPreLoading: true,
				failedTips: {
					type: 'location',
					tips: '获取定位失败!您可以选择手动搜索地址',
					btnText: '手动搜索地址',
					link: '/pages/home/location'
				},
				getLocationStatus: true,
			}
		},
		computed: {
			...mapState([
				'erranderStoreExtra', 'erranderStoreSceneMode', 'erranderStoreSceneOrderId'
			])
		},

		methods: {
			...mapMutations([
				'setState', 'replaceState'
			]),

			onLoad() {
				if(this.mode != this.erranderStoreSceneMode) {
					this.replaceState({key: 'erranderStoreExtra', val: {}});
					this.replaceState({key: 'erranderStoreSceneMode', val: this.mode});
				}
				if(this.order_id > 0) {
					if(this.order_id != this.erranderStoreSceneOrderId) {
						this.replaceState({key: 'erranderStoreExtra', val: {}});
						this.replaceState({key: 'erranderStoreSceneOrderId', val: this.order_id});
					}
				}
				if(this.erranderStoreExtra.notes && !this.erranderStoreExtra.note) {
					this.erranderStoreExtra.note = this.erranderStoreExtra.notes[this.id];
				}
				this.util.request({
					url: 'errander/storeScene/index',
					data: {
						id: this.id,
						mode: this.mode,
						extra: this.erranderStoreExtra,
						order_id: this.order_id,
						forceLocation: 1,
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno != 0) {
						if(result.errno == -2) {
							this.util.$toast(result.message, this.util.getUrl({path: '/plugin/pages/paotui/store/order/index'}), 1000, 'replace');
							return false;
						}
						this.util.$toast(result.message, '', 1000);
						return false;
					}
					result = result.message;
					if(result.buyaddress && result.buyaddress.errno) {
						this.util.$toast(result.buyaddress.message, '', 1000);
					}
					var tempExtra = Object.assign(this.erranderStoreExtra, {
						delivery_day: result.order.delivery_day,
						delivery_time: result.order.delivery_time,
						delivery_nowtime: result.order.delivery_nowtime,
						address_type: result.order.address_type,
						address_input: result.order.address_input,
						buyaddress_id: result.buyaddress_id,
						buyaddress: (result.buyaddress && !result.buyaddress.errno) ? result.buyaddress : {},
						acceptaddress_id: result.acceptaddress_id,
						delivery_tips: result.order.delivery_tips,
						extra_fee: result.order.extra_fee || {},
						goods_weight: result.order.goods_weight,
						goods_type: result.order.goods_type,
						goods_source: result.order.goods_source,
						serial_sn: result.order.serial_sn,
						note: result.order.note,
					});

					this.replaceState({key: 'erranderStoreExtra', val: tempExtra});
					this.goodsType = result.order.goods_type;
					this.goodsSource = result.order.goods_source;
					this.goodsWeight = result.order.goods_weight;
					this.serialSn = result.order.serial_sn;
					this.isdefault = result.order.isdefault;
					this.redPackets = result.redPackets;
					this.buyaddress = result.buyaddress;
					this.acceptaddress = result.acceptaddress;
					this.diy = result.diy;
					this.id = result.diy.id;
					this.basicPart = this.util.extend(this.basicPart, result.basic);
					this.order = result.order;
					if(this.erranderStoreExtra && this.erranderStoreExtra.note) {
						this.note = this.erranderStoreExtra.note;
					}
					if(!this.erranderStoreExtra.extra_fee) {
						this.erranderStoreExtra.extra_fee = {};
					}
					this.setState({type: 'erranderStoreExtra', key: 'agentid', val: result.diy.agentid});
					this.storeNum = result.store_num;
					if(result.diy.data.page.title) {
						this.title = result.diy.data.page.title;
					}
					this.util.setWXTitle(this.title);
					this.islegal = true;
				}).catch((res) => {
					if(window.forceGetLocationStatus == 'fail') {
						this.showPreLoading = false;
						this.getLocationStatus = false;
					}
				})
			},

			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				if(this.erranderStoreExtra.address_type == 'input') {
					if(!this.erranderStoreExtra.address_input.address) {
						this.util.$toast('请填写地址', '', 1500);
						return false;
					}
					if(!this.util.isValidMobile(this.erranderStoreExtra.address_input.mobile)) {
						this.util.$toast('联系人手机号格式不正确');
						return false;
					}
				} else {
					if(this.mode == 'take') {
						if(!this.buyaddress) {
							this.util.$toast('请选择取货地址', '', 1500);
							return false;
						}
						if(!this.util.isValidMobile(this.buyaddress.mobile)) {
							this.$toast('取货联系人手机号格式不正确');
							return false;
						}
					} else if(this.mode == 'send') {
						if(!this.acceptaddress) {
							this.util.$toast('请选择收货地址', '');
							return false;
						}
						if(!this.util.isValidMobile(this.acceptaddress.mobile)) {
							this.$toast('收货联系人手机号格式不正确');
							return false;
						}
					}
				}
				if(this.basicPart.params.type.is_required == 1 && !this.erranderStoreExtra.goods_type) {
					this.util.$toast('请选择物品类型', '', 1500);
					return false;
				}
				this.islegal = false;
				this.util.request({
					url: 'errander/storeScene/submit',
					data: {
						id: this.id,
						mode: this.mode,
						order_id: this.order_id,
						extra: JSON.stringify(this.erranderStoreExtra),
					},
					method: 'POST',
				}).then((res) => {
					var result = res.data.message;
					if(result.errno) {
						if(result.errno == -1000) {
							this.$dialog.confirm({
								title: '温馨提示',
								message: result.message,
								confirmButtonText: '去充值',
								confirmButtonColor: '#ff2d4b'
							}).then(() => {
								this.$router.replace(this.util.getUrl({path: '/plugin/pages/paotui/store/finance/recharge'}));
							}).catch(() => {
								this.islegal = true;
							});
						} else {
							this.islegal = true;
							this.util.$toast(result.message, '', 1000);
						}
						return false;
					}
					var order_id = result.message;
					this.replaceState({key: 'erranderStoreExtra', val: {}});
					this.util.$toast('下单成功', this.util.getUrl({path: '/plugin/pages/paotui/store/order/detail', query: {id: order_id}}), 1500, 'replace');
					return false;
				})

			},

			onCalculate() {
				var params = {
					is_calculate: 1,
					extra: this.erranderStoreExtra,
					id: this.id,
					mode: this.mode,
				};
				this.util.request({
					url: 'errander/storeScene/index',
					data: params,
				}).then((res) => {
					let result = res.data.message;
					if(result.errno != 0) {
						this.util.$toast(result.message, '', 1000);
						if(result.errno == -1000) {
							delete this.erranderStoreExtra.extra_fee[this.erranderStoreExtra.extra_fee.current.pindex][this.erranderStoreExtra.extra_fee.current.cindex];
							this.util.$toast(result.message, '', 1000);
							this.replaceState({key: 'erranderStoreExtra', val: this.erranderStoreExtra});
							return false;
						}
						return false;
					}

					result = result.message;
					if (result.buyaddress && result.buyaddress.errno) {
						this.util.$toast(result.buyaddress.message, '', 2000);
					}
					var tempExtra = Object.assign(this.erranderStoreExtra, {
						delivery_nowtime: result.order.delivery_nowtime,
						address_type: result.order.address_type,
						address_input: result.order.address_input,
						acceptaddress_id: result.acceptaddress_id,
						buyaddress_id: result.buyaddress_id,
						buyaddress: (result.buyaddress && !result.buyaddress.errno) ? result.buyaddress : {},
						extra_fee: result.order.extra_fee,
						redpacket_id: result.order.redpacket_id,
						goods_price: result.order.goods_price,
						delivery_day: result.order.delivery_day,
						delivery_time: result.order.delivery_time,
						delivery_tips: result.order.delivery_tips,
						buyaddress: result.buyaddress,
					});
					this.basicPart = result.basic;
					this.diy.data.items = Object.assign({}, this.diy.data.items);
					this.replaceState({key: 'erranderStoreExtra', val: tempExtra});

					this.redPackets = result.redPackets;
					this.buyaddress = result.buyaddress;
					this.acceptaddress = result.acceptaddress;
					this.order = result.order;
					this.goodsType = result.order.goods_type;
					this.goodsSource = result.order.goods_source;
					this.goodsWeight = result.order.goods_weight;
					this.serialSn = result.order.serial_sn;
					this.isdefault = result.order.isdefault;
				})
			},

			onChangeTypeAndWeight() {
				if(!this.goodsType) {
					this.util.$toast('请选择物品类型');
					return false;
				}
				if(!this.goodsWeight) {
					this.util.$toast('请设置物品重量');
					return false;
				}
				this.setState({type: 'erranderStoreExtra', key: 'goods_weight', val: this.goodsWeight});
				this.setState({type: 'erranderStoreExtra', key: 'goods_type', val: this.goodsType});
				this.setState({type: 'erranderStoreExtra', key: 'isdefault', val: this.isdefault});
				this.onCalculate();
				this.popup.type = false;
			},

			onChangeSourceAndSerialsn() {
				this.setState({type: 'erranderStoreExtra', key: 'goods_source', val: this.goodsSource});
				this.setState({type: 'erranderStoreExtra', key: 'serial_sn', val: this.serialSn});
				this.onCalculate();
				this.popup.source = false;
			},

			onSelectOption(key, value) {
				this[key] = value
			},

			onSelectDay(day) {
				this.setState({type: 'erranderStoreExtra', key: 'delivery_day', val: day});
				this.order = Object.assign({}, this.order);
			},
			
			onSelectTime(time) {
				this.setState({type: 'erranderStoreExtra', key: 'delivery_time', val: time});
				this.erranderStoreExtra.delivery_time = time;
				this.popup.deliveryTime = false;
				this.onCalculate();
			},

			onToggleAddressType(type) {
				this.setState({type: 'erranderStoreExtra', key: 'address_type', val: type});
				this.erranderStoreExtra.address_type = type;
				this.onCalculate();
			},

			onChangeTip() {
				var that = this;
				var value = 0;
				if(that.tipsInfo.dataFrom == 'input') {
					value = parseFloat(that.tipsInfo.input);
				} else {
					value = parseFloat(that.basicPart.tipsGroup[that.tipsInfo.selectIndex]);
				}
				var min = that.basicPart.params.tip.min;
				var max = that.basicPart.params.tip.max;
				if(isNaN(value) || value < min || value > max) {
					that.util.$toast('小费最低' + min + that.Lang.dollarSignCn + ', 最高' + max + that.Lang.dollarSignCn);
					return false;
				}
				that.setState({type: 'erranderStoreExtra', key: 'delivery_tips', val: value});
				that.onCalculate();
				that.popup.tip = false;
			},

			onSelectTips(value, index) {
				this.tipsInfo.inputFocus = false;
				this.tipsInfo.dataFrom = 'select';
				this.tipsInfo.value = value;
				this.tipsInfo.selectIndex = index;
			},

			onTipsInputFocus() {
				this.tipsInfo.inputFocus = true;
				this.tipsInfo.dataFrom = 'input';
				this.tipsInfo.value = this.basicPart.tipsGroup[0];
				this.tipsInfo.selectIndex = 0;
			},

			onBlur() {
				var that = this;
				//处理input框失焦时，页面下部留白的问题
				window.scroll(0, 0);
			},

			onFocus() {
				var that = this;
				//处理input框聚焦时，键盘遮挡页面的问题
				setTimeout(function(){
					document.getElementsByClassName("popup-tip")[0].scrollIntoView();
					//document.body.scrollTop = document.body.scrollTop;
				}, 700);
			},

			onTogglePopup(key) {
				var that = this;
				that.popup[key] = !that.popup[key];
			}
		},
		watch: {

		},
		created() {
			var query = this.util.parseQuery(this.$route.query);
			this.query = query;
			this.id = query.id;
			this.mode = query.mode;
			this.order_id = query.order_id;
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#estore-diy .content{
		padding-bottom: 60px;
	}
	#estore-diy .address{
		padding: 0 15px;
		background-color: #fff;
	}
	#estore-diy .address .address-item{
		position: relative;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
		padding: 15px 0;
	}
	#estore-diy .address .address-item .left{
		width: 10%;
	}
	#estore-diy .address .address-item .left.start{
		align-self: flex-start;
	}
	#estore-diy .address .address-item .mark{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		font-size: 12px;
		color: #fff;
	}
	#estore-diy .address .address-item.from .mark{
		background-color: #ff2d4b;
	}
	#estore-diy .address .address-item.to .mark{
		background-color: #ECAC5C;
	}
	#estore-diy .address .address-item .right{
		width: 90%;
	}
	#estore-diy .address .address-item .icon-right{
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		color: #999;
		font-size: 12px;
	}
	#estore-diy .address .address-item .icon-right~.right{
		padding-right: 20px;
	}
	#estore-diy .address .add-address{
		width: 100%;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
		border: .5px solid #ff2d4b;
	}
	#estore-diy .address .address-item .address-type{
		display: flex;
		align-items: center;
	}
	#estore-diy .address .address-item .address-type .type-item{
		padding: 5px 8px;
		margin-right: 10px;
		font-size: 12px;
		border: 1px solid #ccc;
		border-radius: 2px;
	}
	#estore-diy .address .address-item .address-type .type-item.active{
		color: #FF2D4B;
		border-color: #FF2D4B;
	}
	#estore-diy .address .address-item .inputs .realname{
		width: 30%;
		padding-right: 15px;
	}
	#estore-diy .address .address-item .inputs .mobile{
		width: 70%;
		padding-left: 15px;
	}
	#estore-diy .address .address-item .inputs input{
		display: flex;
		width: 100%;
	}
	#estore-diy .delivery-time-cell .van-cell__value{
		overflow: visible;
		white-space: nowrap;
	}
	#estore-diy .submit-bar{
		position: fixed;
		bottom: 0;
		right: 0;
		left: 0;
		z-index: 10;
		height: 50px;
		background-color: #fff;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	#estore-diy .submit-bar .left{
		width: 65%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 15px;
	}
	#estore-diy .submit-bar .right{
		width: 35%;
		height: 100%;
		background-color: #FF2D4B;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#estore-diy .submit-bar .right.disabled{
		color: #999!important;
		background-color: #eee!important;
		opacity: .5;
	}
	#estore-diy .submit-bar .distance{
		font-size: 12px;
		color: #333;
		margin-top: 5px;
	}
	#estore-diy .submit-bar .final-fee{
		color: #ff2d4b;
		font-size: 18px;
		font-weight: bold;
	}
	/*弹出层*/
	#estore-diy .van-popup{
		font-size: 14px;
	}
	#estore-diy .van-popup .popup-title{
		height: 50px;
		line-height: 50px;
		padding: 0 10px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	/*物品类型*/
	#estore-diy .popup-type .types{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		padding: 10px;
	}
	#estore-diy .popup-type .types .type-item{
		width: 33.3333333333%;
		padding: 5px;
	}
	#estore-diy .popup-type .types .type-item .item-inner{
		width: 100%;
		border-radius: 3px;
		text-align: center;
		font-size: 14px;
		padding: 12px 5px;
		border: 1px solid #ddd;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#estore-diy .popup-type .types .type-item.active .item-inner{
		color: #fff;
		background-color: #FF2D4B;
		border-color: #FF2D4B;
	}

	/*物品来源*/
	#estore-diy .popup-source .serial-sn{
		display: flex;
		align-items: center;
		height: 40px;
		margin: 0 15px 15px;
		border: 1px solid #ddd;
		padding: 5px 10px;
		border-radius: 3px;
	}
	#estore-diy .popup-source .serial-sn input{
		width: 100%;
		height: 100%;
	}
	/*取件时间弹出层*/
	#estore-diy .popup-delivery-time{
		z-index: 20000!important;
	}
	#estore-diy .popup-delivery-time .popup-content{
		height: 300px;
		padding: 0;
	}
	#estore-diy .popup-delivery-time .popup-content>div{
		width: 200px;
		height: 100%;
		overflow-y: auto;
	}
	#estore-diy .popup-delivery-time .popup-content div::-webkit-scrollbar{
		display: none;
	}
	#estore-diy .popup-delivery-time .popup-content .date{
		width: 30%;
		background-color: #f5f5f5;
	}
	#estore-diy .popup-delivery-time .popup-content .date .date-item{
		width: 100%;
		height: 50px;
		line-height: 50px;
		text-align: center;
		font-size: 14px;
	}
	#estore-diy .popup-delivery-time .popup-content .date .date-item.active{
		background-color: #fff;
	}
	#estore-diy .popup-delivery-time .popup-content .time{
		width: 70%;
	}
	#estore-diy .popup-delivery-time .popup-content .time .time-item{
		height: 50px;
		line-height: 50px;
		text-align: center;
		font-size: 14px;
		position: relative;
	}
	#estore-diy .popup-delivery-time .popup-content .time .time-item.active{
		color: #ff2d4b;
	}
	#estore-diy .popup-delivery-time .popup-content .time .time-item .icon{
		position: absolute;
		top: 0;
		right: 10px;
		bottom: 0;
		font-size: 20px;
		color: #ff2d4b;
		display: none;
	}
	#estore-diy .popup-delivery-time .popup-content .time .time-item.active .icon{
		display: block;
	}
	/*小费弹出层*/
	#estore-diy .popup-tip{
		position: absolute!important;
	}
	#estore-diy .popup-tip .tip-group{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		justify-content: space-between;
	}
	#estore-diy .popup-tip .tip-group .tip-item-wrap{
		width: 33.33333333333%;
		padding: 5px;
	}
	#estore-diy .popup-tip .tip-group .tip-item-wrap .tip-item-inner{
		padding: 10px 0;
		text-align: center;
		font-size: 15px;
		border: 1px solid #EAEAEA;
		border-radius: 5px;
	}
	#estore-diy .popup-tip .tip-group .tip-item-wrap .tip-item-inner.active{
		color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#estore-diy .popup-tip .tip-input{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		margin: 5px 5px 0;
		padding: 10px;
		border: 1px solid #EAEAEA;
		border-radius: 5px;
		font-size: 14px;
	}
	#estore-diy .popup-tip .tip-input.active{
		color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#estore-diy .popup-tip .tip-input input{
		display: flex;
		width: 50px;
	}
	/*红包弹出层*/
	#estore-diy .popup-redpacket{
		width: 100%;
		height: 400px;
		overflow: hidden;
	}
	#estore-diy .popup-redpacket .popup-cancle{
		width: 100%;
		height:50px;
		text-align:center;
		line-height:50px;
		font-size:16px;
		color:#FF2D4B;
	}
	#estore-diy .popup-redpacket .popup-container{
		width: 100%;
		height: 300px;
		overflow-y: auto;
		background: #f5f5f5;
	}
	#estore-diy .popup-redpacket .popup-container::-webkit-scrollbar{
		display: none;
	}
	#estore-diy .redPacket-list.content-padded{
		margin: 10px;
	}
	#estore-diy .redPacket-list .row{
		margin-left: -4%;
		overflow: hidden;
	}
	#estore-diy .redPacket-list .text-right{
		text-align: right;
	}
	#estore-diy .redPacket-list .redPacket-list-item{
		background: #fff;
		margin-bottom: 12px;
		overflow: hidden;
		position: relative;
	}
	#estore-diy .redPacket-list .redPacket-list-item .selected-status{
		position: absolute;
		right: 0;
		bottom: 0;
		width: 40px;
	}
	#estore-diy .redPacket-list .redPacket-list-item .redPacket-list-item-container{
		padding: 25px 16px;
	}
	#estore-diy .redPacket-list .redPacket-list-item .redPacket-list-item-container.superredpacket-meal{
		border: 2px solid #ff2d4b;
	}
	#estore-diy .redPacket-list .redPacket-info .col-50{
		width: 46%;
		margin-left: 4%;
		float: left;
	}
	#estore-diy .redPacket-list .redPacket-info .col-50 .redPacket-title{
		color: #ff2d4b;
		margin: 0;
		font-size: 18px;
	}
	#estore-diy .redPacket-list .redPacket-info .col-50 .price{
		color: #ff2d4b;
		position: relative;
		margin-top: -4px;
	}
	#estore-diy .redPacket-list .redPacket-info .col-50 .price .price-num{
		font-size: 22px;
		line-height: 26px;
	}
	#estore-diy .redPacket-list .redPacket-list-item .redPacket-list-item-container .redPacket-use-limit{
		margin-top: 10px;
		color: #666;
		font-size: 14px;
	}
	#estore-diy .redPacket-list .row .col-60{
		width: 55.99999999999999%;
		margin-left: 4%;
		float: left;
		white-space:nowrap
	}
	#estore-diy .redPacket-list .row .col-40{
		width: 36%;
		margin-left: 4%;
		float: left;
	}
	#estore-diy .redPacket-list .row .col-40 .use-condition{
		margin: 0;
		font-size: 14px;
	}
	#estore-diy .redPacket-list .redPacket-list-item .redPacket-list-item-container .other-limit{
		font-size: 12px;
		color: #999;
		border-top: 1px dashed #e0e0e0;
		padding-top: 4px;
	}
	#estore-diy .redPacket-list .redPacket-list-item .circle{
		display: block;
		width: 16px;
		height: 16px;
		background: #f5f5f5;
		position: absolute;
		border-radius: 16px;
		top:50%;
		margin-top: -8px;
		z-index: 20;
	}
	#estore-diy .redPacket-list .redPacket-list-item .circle.superredpacket-meal{
		border: 2px solid #ff2d4b;
	}
	#estore-diy .redPacket-list .redPacket-list-item .circle-left{
		left: -8px;
	}
	#estore-diy .redPacket-list .redPacket-list-item .circle-right{
		right: -8px;
	}
	/*费用明细弹出层*/
	#estore-diy .popup-fee .popup-content{
		height: 220px;
		overflow-y: auto;
	}
	#estore-diy .popup-fee .popup-content::-webkit-scrollbar{
		display: none;
	}
</style>


